<template>
    <div class="pay-box">
        <van-nav-bar title="订单结算" left-text="返回" left-arrow @click-left="back"/>
        <div class="content-box">
            <div class="select-box">
                <div class="select">
                    <div @click="selectAddress">选择地址</div>
                    <van-icon name="arrow"></van-icon>
                </div>
                <div class="s-address" v-show="showAddress">
                    <span class="name">{{showAddress.name}}</span>
                    <span class="phone">{{showAddress.tel}}</span>
                    <span class="mark" v-if="showAddress.isDefault">默认</span>
                    <div class="address-desc">{{showAddress.province+showAddress.city+showAddress.county+showAddress.addressDetail}}</div>
                </div>
            </div>
            <PayBox :sum="sum" :count="count" >
                <template #title>
                    <div class="pay-title">订单信息</div>
                </template>
                <div class="order-items">
                    <OrderItem v-for="(item,index) in goodSData" :key="index" :item="item"></OrderItem>
                </div>
            </PayBox>
            <van-button block round color="#0c34ba" @click="payMoney">立即结算</van-button>
        </div>
        <van-popup
            v-model="isSelectAddress"
            closeable
            position="bottom">
            <div class="select-address-title">选择地址</div>
            <div class="select-address-box">
                <van-address-list
                    :list="list"
                    default-tag-text="默认"
                    @add="addAddress"
                    v-model="chosenAddress"
                />
            </div>
        </van-popup>
    </div>
</template>

<script>
    import '../assets/less/pay.less'
    import PayBox from "../components/PayBox"
    import OrderItem from '../components/OrderItem'
    import {mapMutations} from 'vuex'
    export default {
        name:"Pay",
        data(){
            return {
                //商品ID
                sids:[],
                //地址列表
                list:[],
                //商品信息
                goodSData:[],
                //商品总价格
                sum:0,
                //商品数
                count:0,
                //地址信息
                addressData:[],
                //显示的地址
                showAddress:{},
                //选择地址
                isSelectAddress:false,
                //选择地址的id
                chosenAddress:"",
            }
        },
        methods:{
            ...mapMutations(["changeIsLoadShopbagCount","changeNewOrderMsg"]),
            back(){
                this.$router.go(-1);
            },
            //根据商品sid获取商品信息
            getGoodsData(sids){
                if(!sids){
                    return ;
                }
                let tokenString = this.$cookies.get("tokenString");
                //tokenString不存在，跳转到登录
                if(!tokenString){
                    return this.$router.push({name:'Login'});
                }
                //加载提示
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'GET',
                    url:this.baseUrl + '/commitShopcart',
                    params:{
                        appkey:this.appkey,
                        tokenString,
                        sids:JSON.stringify(sids)
                    }
                }).then( res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //tokenString不正确，跳转到登录
                    if(res.data.code == 700) {
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                        //跳转到登录界面
                        return this.$router.push({name:'Login'});
                    }
                    //tokenString正确
                    if(res.data.code == 50000){
                        this.goodSData = res.data.result;
                        this.totalPrice();
                        this.goodsCount();
                    }
                }).catch(err => {
                    //请求失败
                    //关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            //计算商品总价格
            totalPrice(){
                let sum = 0;
                for(let i = 0; i < this.goodSData.length;i++){
                    sum += parseFloat(this.goodSData[i].price)*this.goodSData[i].count;
                }
                this.sum = sum;
            },
            //计算商品数
            goodsCount(){
                let count = 0;
                for(let i = 0; i < this.goodSData.length;i++){
                    count += this.goodSData[i].count;
                }
                this.count = count;
            },
            //获取地址信息
            getAddressData(){
                let tokenString = this.$cookies.get("tokenString");
                //tokenString不存在，跳转到登录
                if(!tokenString){
                    return this.$router.push({name:'Login'});
                }
                //加载提示
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'GET',
                    url:this.baseUrl + '/findAddress',
                    params:{
                        appkey:this.appkey,
                        tokenString,
                    }
                }).then( res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //tokenString不正确，跳转到登录
                    if(res.data.code == 700) {
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                        //跳转到登录界面
                        return this.$router.push({name:'Login'});
                    }
                    //tokenString正确
                    if(res.data.code == 20000){
                        this.addressData = res.data.result;
                        this.getShowAddress();
                    }
                }).catch(err => {
                    //请求失败
                    //关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            //获取默认地址，无默认地址，则选择第一个
            getShowAddress(){
                for(let i = 0;i < this.addressData.length;i++){
                    if(this.addressData[i].isDefault){
                        this.chosenAddress = this.addressData[i].id;
                        this.showAddress = this.addressData[i];
                        return;
                    }
                }
                if(this.addressData.length == 0){
                    this.showAddress = 0;
                    return ;
                }
                //无默认地址，选择第一个
                this.showAddress = this.addressData[0];
                this.chosenAddress = this.addressData[0].id;   
            },
            //选择地址
            selectAddress(){
                this.isSelectAddress = true;
                this.list = [];
                for(let i = 0;i < this.addressData.length;i++){
                    let o = {};
                    o.id = this.addressData[i].id;
                    o.name = this.addressData[i].name;
                    o.tel = this.addressData[i].tel;
                    o.address = this.addressData[i].province+this.addressData[i].city+this.addressData[i].county+this.addressData[i].addressDetail;
                    o.isDefault = this.addressData[i].isDefault?true:false;
                    this.list.push(o);
                }
            },
            //添加地址
            addAddress(){
                this.$router.push({name:"Address"});
            },
            //立即购买
            payMoney(){
                let tokenString = this.$cookies.get("tokenString");
                //tokenString不存在，跳转到登录
                if(!tokenString){
                    return this.$router.push({name:'Login'});
                }
                //加载提示
                this.$toast.loading({
                    message: '结算中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'POST',
                    url:this.baseUrl + '/pay',
                    data:{
                        appkey:this.appkey,
                        tokenString,
                        sids:JSON.stringify(this.sids),
                        phone:this.showAddress.tel,
                        address:this.showAddress.province+this.showAddress.city+this.showAddress.county+this.showAddress.addressDetail,
                        receiver:this.showAddress.name
                    }
                }).then( res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //tokenString不正确，跳转到登录
                    if(res.data.code == 700) {
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                        //跳转到登录界面
                        return this.$router.push({name:'Login'});
                    }
                    //tokenString正确
                    if(res.data.code == 60000){
                        //重新刷新购物车数据
                        this.changeIsLoadShopbagCount(false);
                        this.changeNewOrderMsg(true);
                        this.$router.push({name:"Shopcar"});
                    }
                }).catch(err => {
                    //请求失败关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            }
        },
        components:{
            PayBox,
            OrderItem
        },
        created(){
            this.sids = this.$route.params.sids;
            if(!this.sids){
                // this.$router.push({name:'Shopcar'});
            }
            this.getGoodsData(this.sids);
            this.getAddressData();
        },
        watch:{
            chosenAddress(newV){
                //修改显示的地址
                for(let i = 0;i < this.addressData.length;i++){
                    if(this.addressData[i].id == newV){
                        this.showAddress = this.addressData[i];
                        break;
                    }
                }
            }
        }
    }
</script>
